

















import { Component, Vue, Prop } from "vue-property-decorator";

@Component({
  components: {},
})
export default class GroupButton extends Vue {
  @Prop() id!: number | null;
  selected: number | null = null;

  buttons = [
    {
      title: "EXCELLENT",
      locale: "ОТЛИЧНО",
      class: "left-btn",
      selected: "green",
      value: 0,
    },
    {
      title: "SATISFACTORILY",
      locale: "УДОВЛ.",
      class: "center-btn",
      selected: "yellow",
      value: 1,
    },
    {
      title: "BADLY",
      locale: "ПЛОХО",
      class: "right-btn",
      selected: "red",
      value: 2,
    },
  ] as {
    title: string;
    locale: string;
    class: string;
    selected: string;
    value: number;
  }[];

  created() {
    this.selected = this.id;
  }

  onClick(v: number | null) {
    if (this.selected === v) {
      this.selected = null;
    } else {
      this.selected = v;
    }
    this.$emit("click", this.selected);
  }
}
